"use client"

import { useAuth } from "@/lib/auth-context"
import { UserRole } from "@/lib/types"
import { Button } from "@/components/ui/button"
import {
  DropdownMenu,
  DropdownMenuContent,
  DropdownMenuItem,
  DropdownMenuTrigger,
} from "@/components/ui/dropdown-menu"
import { Badge } from "@/components/ui/badge"
import { ChevronDown, Shield } from "lucide-react"

const roleLabels: Record<UserRole, string> = {
  super_admin: "Super Admin",
  procurement: "Procurement",
  packing: "Packing",
  delivery: "Delivery",
  custom: "Custom"
}

const roleColors: Record<UserRole, "default" | "secondary" | "destructive" | "outline"> = {
  super_admin: "destructive",
  procurement: "default",
  packing: "secondary",
  delivery: "outline",
  custom: "outline"
}

export function RoleSwitcher() {
  const { user, setUser } = useAuth()

  const switchRole = (newRole: UserRole) => {
    if (user) {
      localStorage.setItem("userRole", newRole)
      setUser({ ...user, role: newRole })
    }
  }

  if (!user) return null

  return (
    <DropdownMenu>
      <DropdownMenuTrigger asChild>
        <Button variant="outline" size="sm" className="gap-2">
          <Shield className="h-4 w-4" />
          <Badge variant={roleColors[user.role]} className="text-xs">
            {roleLabels[user.role]}
          </Badge>
          <ChevronDown className="h-4 w-4" />
        </Button>
      </DropdownMenuTrigger>
      <DropdownMenuContent align="end">
        {Object.entries(roleLabels).map(([role, label]) => (
          <DropdownMenuItem
            key={role}
            onClick={() => switchRole(role as UserRole)}
            className={user.role === role ? "bg-accent" : ""}
          >
            <Badge variant={roleColors[role as UserRole]} className="mr-2 text-xs">
              {label}
            </Badge>
            {user.role === role && "✓"}
          </DropdownMenuItem>
        ))}
      </DropdownMenuContent>
    </DropdownMenu>
  )
}
